import AttributeInfo from "./attributeInfo/index.js"
import DrawingList from "./drawingList/index.js"
import PlmTree from "./plmTree/index.js"
import DrawingListCopy from "./drawingListCopy/index.js"
import PlmTreeCopy from "./plmTreeCopy/index.js"

const components = [
    AttributeInfo,
    DrawingList,
    PlmTree,
    DrawingListCopy,
    PlmTreeCopy

]

const install = function(Vue) {
    if(install.installed) return
    components.map(component => Vue.component(component.name, component))
}

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}
export {
    AttributeInfo,
    DrawingList,
    PlmTree,
    DrawingListCopy,
    PlmTreeCopy
}
export default {
    install,
    AttributeInfo,
    DrawingList,
    PlmTree,
    DrawingListCopy,
    PlmTreeCopy
}
